html,
body,
p,
h1,
h2,
h3 {
  margin: 0;
}
table {
  border-collapse: collapse;
}
h2 {
  margin-bottom: 20px;
  margin-top: 100px;
}
header {
  background: rgb(77, 75, 233);
  padding: 30px;
  color: #fff;
  text-align: center;
  font-size: 1.6em;
}
main {
  width: 70%;
  margin: 100px auto;
}
main .ko {
}

main .ko div {
  width: 100%;
  display: table;
  border-collapse: collapse;
  text-align: center;
}
main .ko div:nth-of-type(1) {
  font-size: 1.2em;
  font-weight: bold;
}
main .ko div:nth-of-type(2) {
  margin-top: 10px;
  background: #f3f3f3;
}
main .ko div span {
  display: table-cell;
  padding: 50px;
  border: 1px solid #ddd;
  width: 33%;
}
main .ko div:nth-of-type(2) span {
  padding: 15px;
}
main .area div {
  border: 1px solid #ddd;
  background: #f5f5f5;
  text-align: center;
  padding: 60px;
}
main .area div select {
  min-width: 200px;
  padding: 10px;
}
main .area div table {
  width: 100%;
  background: #fff;
  margin-top: 60px;
}
main .area div table caption {
  margin: 20px;
  font-weight: bold;
  font-size: 3em;
}
main .area div table th,
main .area div table td {
  padding: 15px;
  border: 1px solid #000;
}
.chart {
  height: 500px;
  margin: 0 auto;
}
